<template>
    <div class="all-wrapper menu-side with-pattern">
        <div class="auth-box-w">
            <div class="logo-w">
                <router-link to="/">
                    <img alt="" src="img/logo-big.png"/>
                </router-link>
            </div>
            <h4 class="auth-header">
                Concluir cadastro
            </h4>
            <form @submit.prevent="register">

                <div class="form-group">
                    <label for="">Email</label>
                    <input class="form-control" placeholder="Seu email" type="email" v-model="email" required disabled/>
                    <span class="text-danger" v-if="errors.email"><small>{{errors.email}}</small></span>
                    <div class="pre-icon os-icon os-icon-mail"></div>
                </div>
                <div class="form-group">
                    <label for="">Nome</label>
                    <input class="form-control" placeholder="Seu nome" type="text" v-model="name" required/>
                    <span class="text-danger" v-if="errors.name"><small>{{errors.name}}</small></span>

                    <div class="pre-icon os-icon os-icon-user-male-circle"></div>
                </div>

                <div class="form-group">
                    <label for="">Senha</label>
                    <input class="form-control" placeholder="Sua senha" type="password" v-model="password" required
                           minlength="6"/>
                    <span class="text-danger" v-if="errors.password"><small>{{errors.password}}</small></span>
                    <div class="pre-icon os-icon os-icon-fingerprint"></div>
                </div>
                <div class="form-group">
                    <label for="">Confirmar senha</label>
                    <input class="form-control" placeholder="Sua senha" type="password" v-model="password_confirmation"
                           required minlength="6"/>
                    <span class="text-danger" v-if="errors.password_confirmation"><small>{{errors.password_confirmation}}</small></span>
                    <div class="pre-icon os-icon os-icon-fingerprint"></div>
                </div>
                <div class="buttons-w">
                    <button class="btn btn-primary btn-block">Finalizar</button>
                </div>

                <p class="text-center">
                    <router-link to="/entrar">Já tenho cadastro</router-link>
                </p>
            </form>
        </div>
    </div>
</template>
<script>
    import {INVITATION_USER_DATA_URL, INVITATION_CREATE_USER_URL} from './../../constants/urls'

    export default {
        data() {
            return {
                name: '',
                email: '',
                password: '',
                password_confirmation: '',
                errors: {}
            }
        },
        methods: {
            register() {
                this.preload(true);
                this.axios.post(INVITATION_CREATE_USER_URL(this.$route.params.token), {
                    name: this.name,
                    email: this.email,
                    password: this.password,
                    password_confirmation: this.password_confirmation
                }).then((response) => {
                    this.$store.commit('SET_USER', response.data);
                    this.$store.commit('SET_TOKEN', response.data.data.token);
                    setTimeout(() => {
                        this.$router.push({name: 'admin.index'});
                        this.$snotify.success('Seja bem vindo!', 'Sucesso');
                    }, 500)
                }).catch((err) => {
                        if (err.response.status === 500 || !response.status) {
                            this.$router.push({name: 'home'});
                            this.$Snotify.error("Erro desconhecido")
                        } else {
                            this.errors = err.response.data.errors;
                        }
                    }
                )
                    .finally(
                        () => setTimeout(() => this.preload(false))
                    );
            }
        },
        mounted() {
            this.$store.commit('LOGOUT');
            this.$store.commit('SET_TOKEN', null);
            localStorage.clear();
            axios.get(INVITATION_USER_DATA_URL(this.$route.params.token)).then(response => {
                this.email = response.data.email;
                dd(response)
            }).catch(e => {
                this.$router.push({name: 'login'});
            }).finally(() => this.preload(false));
        }

    }
</script>
<style lang="scss">


    p.text-center {
        a {
            margin: 10px;
            &:hover {
                text-decoration: none;
                color: #0f1115;
            }
        }
    }

    @media screen and (max-width: 760px) {
        .all-wrapper.menu-side.with-pattern {
            margin-top: 6%;
        }
    }

</style>